.nav-bar {

    position: fixed;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* padding-left: 2rem; */
    width: 100vw;
    background: rgb(2, 91, 61);
}

.logo {
    font-size: 2.5rem;
    color: rgb(230, 0, 0);
    margin: .5rem;
}

.nav-list {
    /* display: flex; */
}

.nav-list ul {
    display: flex;
    transition: 1s;
    gap: 1.5rem;
    padding: 1rem;
    align-items: center;

}

.nav-list li {
    list-style: none;
    height: min-content;
}

.nav-list li a {
    display: inline-block;
    text-decoration: none;
    font-size: 1.5rem;
    color: aliceblue;
    text-transform: uppercase;
}

.button {
    display: none;
    user-select: none;
    appearance: none;
    border: none;
    outline: none;
    background: none;
    margin: 15px;
    position: absolute;
    top: 0;
    right: 0;
}

.button span {
    display: block;
    background-color: rgb(255, 255, 255);
    width: 30px;
    height: 4px;
    margin-bottom: 6px;
    border-radius: 6px;
    transform-origin: 0 0;
    transition: 0.4s;
}

.button:hover span:nth-child(2) {
    transform: translateX(8px);
    background-color: red;
}

.button.active span:nth-child(1) {
    transform: translate(5px, 2px) rotate(45deg);
}

.button.active span:nth-child(2) {
    opacity: 0;
    transform: translateX(15px);
    /* display: none; */
}

.button.active span:nth-child(3) {
    transform: translate(2px, 3px) rotate(-45deg);
}

.buy {
    background-color: white;
    border-radius: 10%;
}

#buy a {
    color: black;
    padding: .5rem;
    font-size: 1.5em;
}

@media (max-width : 600px) {
    .button {
        display: block;
    }

    .nav-list {
        width: 100%;
    }

    .nav-list ul {
        display: none;
        flex-direction: column;
        width: 100%;
        text-align: center;
    }

    .nav-bar {
        background-color: rgba(0, 0, 0, 0.9);
        backdrop-filter: saturate(180%) blur(10px);
        flex-direction: column;
        align-items: flex-start;
    }

    .nav-list.active ul {
        display: flex;
    }

    #cursor {
        display: none;
    }
}