.main-about {
    display: none;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    background-image: url('/static/icons/bc10.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    backdrop-filter: saturate(180%) blur(10px);
    justify-content: center;
    opacity: 0;
    transition: 0.8s ease;
}

.info2 {
    margin-top: 40px;
}

.goal {
    color: greenyellow;
    max-width: 80vw;
    font-size: 1.8rem;
    text-align: center;
}

.main-token {
    display: none;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
    background: rgba(158, 42, 42, 0.8);
    background-image: url('/static/icons/bc11.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    backdrop-filter: saturate(180%) blur(10px);
    justify-content: center;
    opacity: 0;
    transition: 0.8s ease;
}

.main-road {
    display: none;
    overflow-y: scroll;
    overflow-x: hidden;
    width: 100vw;
    height: 100vh;
    background: rgba(158, 42, 42, 0.8);
    background-image: url('/static/icons/bc12.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    backdrop-filter: saturate(180%) blur(10px);
    justify-content: center;
    opacity: 0;
    transition: 0.8s ease;
}

.scroll {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* gap: 2rem; */
    /* min-width: 50vw; */
    position: fixed;
}

.clicked {
    /* overflow-x: hidden; */
    opacity: 1;
    display: flex;
}

.top {
    width: 100vw;
    height: 20vh;
}

.bottom {
    width: 100vw;
    height: 20vh;
}

.mainLogo {
    margin-top: 1rem;
    width: 400px;
    height: 400px;

    background-image: url('/static/icons/logo.png');
    background-position: center;
    background-size: cover;
}

.about-info {
    color: whitesmoke;
    max-width: 80vw;
}

.about-head {
    color: rgb(197, 153, 11);
    font-size: 50px;
    text-align: center;
    margin: 2.5rem 0;
}

.about-head span {
    color: #00CC8E;
}

.about-social {
    margin: 50px 0;
    color: rgb(197, 153, 11);
    font-size: 50px;
}

.about-links {
    display: flex;
    gap: 20px;
}

.about-links .items {
    width: 70px;
    height: 70px;
}

.token-tit {
    position: fixed;
    z-index: 10000;
    top: 10%;
    left: 50%;
    color: #00CC8E;
    font-size: 3.5rem;
    text-align: center;
    transform: translateX(-50%);
}

.main-token {
    /* justify-content: center; */
    align-items: center;
}

.tax-tnx {
    gap: 2rem;
}

.main-token .scroll {
    flex-direction: row;

    gap: 2rem;
}

.boxs {
    margin-top: 50px;
    width: 300px;
    height: 350px;
    padding: 2em;
    background-color: #043d57b3;
    border-radius: 20%;
    text-align: center;
    color: aliceblue;
}

.about-info h1 span {
    color: orange;
    font-size: 2.5rem;
}


.boxs h1 {
    font-size: 2.5rem;
    margin-bottom: 20px;

}

.boxs h2 {
    /* text-align: left; */
    color: black;
    margin-bottom: 20px;
}

.boxs span {
    color: lightblue;
    font-size: 2rem;
}

.tax-tnx {
    display: flex;
    flex-direction: column;
    align-items: center;
}

table {
    width: 100%;
    height: 80%;
    font-size: 1.6rem;
    color: lightblue;
}

table .term {
    font-size: 1.4rem;
    /* text-align: left; */
    color: black;
}

.token-info h2 {
    text-align: left;
}

@media (max-width : 600px) {
    .main-token .scroll {
        flex-direction: column;
    }

    .main-token {
        align-items: baseline;
    }

    .top {
        height: 30vh;
    }

    .token-tit {
        top: 20%;
    }
}



.close {

    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: none;
    background-color: rgb(255, 255, 255);
    background-image: url('/static/icons/back.svg');
    background-position: center;
    background-size: cover;
    position: fixed;
    top: 13%;
    left: 10%;
}



/* ROADMAP */

.phases {
    margin-top: 50px;
    display: flex;
    gap: 40px;
}

@media (max-width : 1100px) {
    .phases {
        flex-direction: column;
    }
}


.phs {
    padding: 50px;
    background-color: #fe3887;
    text-align: center;
    border-radius: 15%;
    width: 250px;
    height: 300px;
}

.phs h1 {
    font-size: 2rem;
    color: #b72818;
}

.phs ul {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 100%;
    height: 100%;
    gap: 10px;
}

.phs ul li {
    list-style: disc;
    line-height: 2rem;
    font-size: 1.5rem;
    color: #1f0619;
}

.phs ul .right {
    list-style: none;
    list-style-type: 'âœ“';
}

.phs3 ul li {
    /* list-style: none; */

}

.about-whitepaper {
    text-align: center;
    font-size: 0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
    font-style: none;
    font-weight: 1000;
}

.about-social {
    margin-top: 100px;
}