@import url('https://fonts.googleapis.com/css2?family=Lora:ital@1&family=Play:wght@700&family=Saira+Condensed:wght@500;600&display=swap');

* {
    margin: 0;
    padding: 0;
}

html,
body {
    font-family: 'Play', sans-serif;
    overflow: hidden;
    background-color: rgb(0, 0, 0);
}

.webgl {
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.social-list {
    margin: 2rem;
    margin-right: 0;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    /* gap: 1rem; */
    position: fixed;
    bottom: 0;
    right: 0;
    background-color: rgb(2, 91, 61);
}

.allLinks {
    border-bottom: 2px solid rgb(8, 190, 129);
    /* border-top: none; */
}

.about-links .items {
    background-color: white;
    border-radius: 50%;
}

.twitter {
    height: 50px;
    width: 50px;
    /* background-color: white;
    border-radius: 50%; */
    background-image: url('../static/icons/twitter.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.mail {
    height: 50px;
    width: 50px;
    /* background-color: white; */
    /* border-radius: 50%; */
    background-image: url('../static/icons/email.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;

}

.poo {
    height: 50px;
    width: 50px;
    /* background-color: white; */
    /* border-radius: 50%; */
    background-image: url('../static/icons/poo.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;

}

.telegram {
    height: 50px;
    width: 50px;
    /* background-color: rgb(255, 255, 255); */
    /* border-radius: 50%; */
    background-image: url('../static/icons/telegram.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.bsc {
    height: 50px;
    width: 50px;
    /* background-color: rgb(255, 255, 255); */
    /* border-radius: 50%; */
    background-image: url('../static/icons/bsc.png');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.whitepaper {
    position: fixed;

    bottom: 0;
    left: 0;

    color: white;
    display: flex;
    justify-content: center;
    align-items: center;

}

.extra {
    padding: 15px;
    margin: 20px;
    background-color: rgb(2, 91, 61);
    font-size: 1.5rem;
    border-radius: 20%;
    font-style: none;
    color: white;
}

.extra-info {
    color: white;
    height: 50px;
    width: 50px;
    /* background-color: rgb(255, 255, 255); */
    /* border-radius: 50%; */
    background-image: url('../static/icons/info.svg');
    background-size: 40px 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgb(2, 91, 61);
    font-size: 1.5rem;
    border-radius: 20%;
}

.cursor {
    width: 30px;
    height: 30px;
    display: none;
    position: fixed;
    background-color: white;
    border-radius: 50%;
    transition: 0.1s ease;
    transform: translate(-50%, -50%);
    pointer-events: none;
    mix-blend-mode: difference;
    z-index: 100000;
}

.hovered {
    width: 60px;
    height: 60px;
}

.ca {
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: fixed; */
    bottom: 5%;
    right: 10%;
    gap: 10px;
    border-radius: 10%;
}

.text {
    color: rgb(0, 0, 0);
    background-color: aliceblue;
    border-radius: 10%;
    padding: 10px;
    max-width: 50vw;
    font-size: 1vw;
}

.ca button {
    padding: 10px;
    font-size: 20px;
    font-weight: 1000;
}

#copybtn {
    border-radius: 10%;
}


@media (max-width : 600px) {
    .text {
        font-size: 2vw;
    }

    .partners {
        flex-direction: column;
        gap: 2rem;
    }
}

.banner {
    display: none;
    position: fixed;
    z-index: 10000;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    background-image: url('/static/icons/bc13.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.banner-info {
    display: flex;
    margin-top: 40px;
}

.info-head {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info-head h1 {
    color: rgb(0, 252, 168);
    font-size: 2rem;

}

.info-head h2 {
    color: rgb(252, 206, 0);
    font-size: 1rem;

}

.banner-cancel {
    width: 50px;
    height: 50px;
    border: 10px solid rgb(255, 255, 255);

    position: fixed;
    right: 5%;
    top: 2%;
    z-index: 10000;

    border-radius: 50%;

    background-image: url('/static/icons/cancel.svg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

}

.banner-img {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.wikirat {
    width: 200px;
    height: 200px;

    background-image: url('/static/icons/logo.png');
    background-position: center;
    background-size: cover;
}

.cross {
    width: 50px;
    height: 50px;

    background-image: url('/static/icons/cancel.svg');
    background-position: center;
    background-size: cover;
}

.parts {
    display: flex;
    /* flex-direction: column; */
    align-items: center;
    gap: 1rem;
}

.azbit {
    height: 80px;
    width: 180px;

    background-image: url('/static/icons/azbit.png');
    background-position: center;
    background-size: cover;

}

.pancake {
    height: 100px;
    width: 100px;

    background-image: url('/static/icons/pcake.svg');
    background-position: center;
    background-size: cover;
}

.trades {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
}

.trades a {
    font-style: none;
}

.trade {
    background-color: rgb(176, 170, 76);
    color: white;
    font-weight: 1000;
    font-style: none;
    text-align: center;
    text-decoration: none;

    padding: 20px;
    margin: 20px;
    border-radius: 10px;
    border: none;


}

@media (min-width : 600px) {
    .banner-img {
        flex-direction: row;
        margin-top: 50px;
        align-items: center;
    }

    .wikirat {
        width: 300px;
        height: 300px;
        margin: 0;
    }

    .azbit {
        width: 300px;
        height: 100px;
    }

    .parts {
        flex-direction: column;
        gap: 50px;
    }
}

.partners {

    display: flex;


}

.part {


    background-size: contain;
    background-position: center;
    background-repeat: no-repeat
}

.binance {
    width: 300px;
    height: 200px;
    background-image: url('/static/icons/binance.png');
}

.pcake {
    width: 300px;
    height: 200px;
    background-image: url('/static/icons/pcake.svg');
}

.az {
    width: 300px;
    height: 200px;
    background-image: url('/static/icons/azbit.png');
    background-color: white;
}